@import url('https://fonts.googleapis.com/css2?family=Roboto:ital@1&display=swap');

*{
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
   top : 50%;
   left : 50%;
   position : absolute;
   transform : translate(-50% , -50%);
   margin: 0 auto;
   width: 250px;
   text-align: center;
   border: 10px solid black;
   background: black;
   border-radius: 14px 14px 14px 14px;
   box-shadow: 0px 10px 13px -7px #000000, 16px 27px 46px 0px rgba(0,0,0,0.45);
}

.keypad{
  display: grid;
  grid-template-columns: repeat(4 , 1fr);
  grid-auto-rows: minmax(60px , auto);
}

input[type = "text"]{
  height: 75px;
  width: 240px;
  background-color: black;
  border : 0px;
  color: #ffffff;
  text-align: right;
  font-size: 25px;
  font-weight: 500px;
  letter-spacing: 1px;
}

#clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
}

#result{
  grid-column: 3/5;
  grid-row: 5;
  color : #262834;
}

#backspace{
  color : #262834;
}

button{
   margin: 5px;
   border: none;
   border-radius: 5px;
   cursor: pointer;
   background: #262834;
   color: #ffffff;
   font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active{
  outline: none;
}

.heighlight{
  background: rgb(189, 98, 38);
}